* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h1 {
  margin: 0;
  font-size: 100%;
}

.header {
  background-color: #002419;
  padding-left: 30px;
  height: 50px;
  color: white;
  font-size: 25px;
  line-height: 50px;
  vertical-align: middle;
}

.wrapper {
  height: calc(100vh - 50px);
  background-color: #F5F5F6;
  padding-top: 20px;
  padding-left: 20px;
}

.app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.control {
  margin-top: 5px;
  float: right;
  cursor: pointer;
  margin-right: 5px;
}



